      margin-right: 0;
      animation: grow 500ms linear 0s infinite reverse; }

@keyframes grow {
  from {
    opacity: 0;
    transform: scale(0, 0); }
  to {
    opacity: 1;
    transform: scale(1, 1); } }

@keyframes move {
  from {
    transform: translateX(0); }
